import logo from './logo.svg';
import './App.css';
import {Navbar, Container, Nav, NavDropdown, Button} from 'react-bootstrap'
import { useState } from 'react';
import Data from './data.js'
import { Link, Route, Switch } from 'react-router-dom'
import Detail from './component/Detail.js'
import { ReactDOM } from 'react-dom';
import "./App.scss"
import axios from 'axios'
/////////////////////////////////////////////////////////////////////////////////////////////////////////

function App() {
  let [shoes, shoes변경] = useState(Data)
  let [clickBtn,  clickBtnSet] = useState(2)
/////////////////////////////////////////////////////////////////////////////////////////////////////////

  return (
  <div className="App">

    <Navbar bg="dark" variant="dark" className='navbar2'>
      <Container>
        <Navbar.Brand href='/'>Navbar</Navbar.Brand>
        <Nav className="me-auto" >
          <Nav.Link as={Link} exact to="/">Home</Nav.Link>
          <Nav.Link as={Link} to="/detail/">Detail</Nav.Link>
          <Nav.Link href="#pricing">Pricing</Nav.Link>
        </Nav>
      </Container>
    </Navbar>
{/* ///////////////////////////// */}

 {/* ///////////////////////////////////////////////////////////////////////////////////////////////////////////////      */}
    <Switch>
    <Route exact path="/">
    <div className='jumbotron'>
        <h1>Hello, world!</h1>
        <p>
          This is a simple hero unit, a simple div-style component for calling
          extra attention to featured content or information.
        </p>
        <p>
          <Button variant="primary">Learn more</Button>
        </p>
      </div>

      <div className='container'>
        <div className='row'>
          { shoes.map(function(a,i){
            return(
                <Item shoes={shoes[i]} i={i}/>
              )
          })}
      </div>
    </div>
      <button className='btn btn-primary' onClick={()=>{
        axios.get("https://codingapple1.github.io/shop/data"+ clickBtn +".json")
        .then((e)=>{
        shoes변경([...shoes, ...e.data])
         })
        .catch(()=>{
        });
        clickBtnSet( clickBtn + 1 )    
      }}>더보기</button>
    </Route>
{/* /////////////////////////////////////////////////////////////////////////////////////    */}
    <Route path='/detail/:id'>
      <Detail shoes={shoes}></Detail>
    </Route>
  </Switch> 



{/* ///////////////////////////////////////////////// */}
    </div>
  );
}
function Item(props){
  return(
    <div className='col-md-4'>
      <Link to={"/detail/"+ props.shoes.id } className="link_detail">
          <img src={'https://codingapple1.github.io/shop/shoes'+ (props.i+1) +'.jpg'} width='100%'/>
          <div className='item_content'>
            <h4>{props.shoes.title}</h4>
            <p>{props.shoes.content}</p>
            <p>{props.shoes.price}</p>
          </div>
      </Link>
    </div>
  )
}

function more(props){
  axios.get("https://codingapple1.github.io/shop/data"+ props.clickBtn +".json")
  .then((e)=>{
    props.shoes변경([...props.shoes, ...e.data])
  })
  .catch(()=>{
  });
  props.clickBtnSet( props.clickBtn + 1 )
}

/////////////////////////////////////////////////////////////////////////////////
export default App;
